
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

html,body {
     width: 100%;
     height: 100%;
     margin: 0;
     padding: 0;
}
body {
    min-width: 1024px;
    min-height: 600px;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none; /* 禁止鼠标拖动页面时选中页面文字 */
}
.AddNewBooks{
  padding: auto;
	width:1600px;
	height:900;
	MARGIN-RIGHT: auto;
	MARGIN-LEFT: auto;
  position: absolute;
  /* border:0.2rem solid #4d4d4d */
}
.Header {
    padding: 0% 10% 1%;
    /* border: 0.2rem solid #4d4d4d; */
    cursor: pointer;
    text-transform: capitalize;
    margin: 0% 5% 0% 18%;
    float: left;
}
.back{
    padding: auto;
    margin: 3% 10% 1% 5%;
    float: right;
    font-size: 200%;
}
.bookid{
    padding: auto;
    /* border:0.1rem solid #4d4d4d; */
    margin: 5% 5% 0% 5%;
    float: left;
    font-size: 170%;
}
.bookname{
    padding: auto;
    /* border:0.1rem solid #4d4d4d; */
    margin: 5% 0% 0% 0%;
    float: left;
    font-size: 170%;
}
.category{
    padding: auto;
    /* border:0.1rem solid #4d4d4d; */
    margin: 5% 0% 0% 0%;
    float: left;
    font-size: 170%;
}
.remain{
    margin:5% 15% 0% 0%;
    font-size: 170%;
    float: right;
    /* border: 0.1rem solid #4d4d4d; */
}
.introduction{
    padding: auto;
    /* border:0.1rem solid #4d4d4d; */
